// - - - - About Us Page Styling - - - - //

.about
  // Column Layouts
  &__first-column
    padding-right: 20px
  &__second-column
    @media (min-width: $breakpoint-tablet)
      border: 1px solid $black-200
      
  // Side Scroll
  &__scroll
    &-item
      text-align: right
      transition: color .3s ease
      &:hover
        color: $brand-primary-500
      &--active
        @extend .about__scroll-item
        color: $brand-primary-500
        &:after
          color: $brand-primary-500

  // Sections
  &__intro, &__philosophy, &__process, &__team
    padding: 0 20px
  &__stack
    padding: 0

  // Stack Section Specific Styling
  &__title
    @extend %H2
    padding-left: 20px
  &__filters
    padding: 0 0 20px 0 
    list-style: none
    @media (min-width: $breakpoint-tablet)
      padding-right: 20px
    &-item
      color: $brand-primary-500
      font-family: 'decima_monoregular'
      font-size: 12px
      text-transform: uppercase
      &:hover
        text-decoration: underline
      &--active
        @extend .about__filters-item
        color: $black-300
  &__stack-grid
    padding: 0 20px

// Image Grid
.about-grid
  &__image
    background-color: $black-100
  &__text
    &-header
      font-size: 12px
      text-transform: uppercase
    &-sub-header
      font-size: 12px
      font-style: italic
      color: $black-400

