// - - - - Header Layout - - - - //
.header-container
  +clearfix
  margin: 0 auto
  @media (min-width: $breakpoint-tablet)
    max-width: $breakpoint-tablet
  @media (min-width: $breakpoint-desktop)
    max-width: $breakpoint-desktop

.header
  +clearfix
  position: relative
  z-index: 5000
  top: 0
  left: 0
  right: 0
  height: 60px
  &__logo
    float: left
    margin-top: -3px
  &__mobile-menu
    float: right
    margin-top: 2px
    @media (min-width: $breakpoint-tablet)
      display: none
  &__tablet-menu
    display: none
    @media (min-width: $breakpoint-tablet)
      display: inline
  &__nav-list
    float: right
  &__nav-item
    display: inline-block

/* Effect 1: Brackets */
.header-hover
  a
    &:before, &:after
      display: inline-block
    &:before
      margin-right: 10px
    &:after
      margin-left: 10px 