// Type Syles

body
  font-family: decima_monoregular
  color: $black-500

%text-button
  font-family: decima_monobold
  font-size: 12px
  color: $brand-primary-500
  letter-spacing: 1.29px
  line-height: 14px
  text-decoration: none
  text-transform: uppercase
  cursor: pointer
  transition: padding-left 2s ease
  a
    text-decoration: none
    color: $brand-primary-500
  &:after
    +materialicon($long-arrow-right)
    padding-left: 5px
  &:hover
    text-decoration: underline

%section-title
  font-family: decima_monobold
  font-size: 12px
  letter-spacing: 1.29px
  line-height: 14px
  text-transform: uppercase

%author-name
  font-family: decima_monoitalic
  font-size: 12px
  color: $black-400
  line-height: 14px
  &:before
    @extend %author-name
    content: "by"
    padding-right: 6px

%H1
  font-size: 30px
  letter-spacing: 0.14px
  line-height: 30px

%H2
  font-size: 20px
  line-height: 20px
  text-transform: uppercase

%H3
  font-family: decima_monobold
  font-size: 16px
  line-height: 17px
  text-transform: uppercase

%hero-text
  color: white
  font-size: 40px
  letter-spacing: 0.19px
  line-height: 40px
  text-transform: uppercase
  @media (min-width: $breakpoint-desktop)
    font-size: 60px
    letter-spacing: 0.19px
    line-height: 60px


%intro-text
  font-size: 22px
  letter-spacing: 0.1px
  line-height: 21px
  @media (min-width: $breakpoint-desktop)
    font-size: 28px
    line-height: 28px

%title-link
  font-size: 16px
  color: $brand-primary-500
  line-height: 22px
  cursor: pointer
  &:hover
    text-decoration: underline
  @media (min-width: $breakpoint-desktop)
    font-size: 18px
    line-height: 24px

%body-text
  font-family: 'roboto-mono_regular'
  font-size: 13px
  color: $black-400
  line-height: 22px

%body-into-text
  font-size: 18px
  line-height: 22px

%work-title
  font-size: 5vw

%date
  font-size: 12px
  line-height: 14px

%blockquote
  border-left: 1px solid $brand-primary-500
  margin: 20px
  padding: 0.5em 10px
  p
    display: inline
    font-family: DecimaMono-Italic
    font-size: 18px
    color: $black-500
    line-height: 18px

.sub-page-header
  @extend %H1
  border-bottom: 1px solid $black-200
  margin-top: 40px
  padding: 0 20px 20px 20px
  text-transform: uppercase
  box-sizing: outline-box


// Element Styling

h1
  @extend %H1

h2
  @extend %H2

h3
  @extend %H3
  
p
  @extend %body-text
  margin-bottom: 20px
  &:first-child
    margin-top: 40px

blockquote
  @extend %blockquote

ul
  @extend %body-text
  color: $black-500
  margin: 20px 0
  list-style-type: square

ol
  @extend %body-text
  color: $black-500
  margin: 20px 0

img
  margin-left: auto
  margin-right: auto