.container
  +clearfix
  clear: both
  margin: 0 auto
  min-height: 100%
  @media (min-width: $breakpoint-tablet)
    max-width: $breakpoint-tablet
  @media (min-width: $breakpoint-desktop)
    max-width: $breakpoint-desktop
  &-blog
    +clearfix
    clear: both
    margin: 0 auto 20px auto
    padding: 0 20px
    min-height: 100%
    @media (min-width: $breakpoint-tablet)
      max-width: $breakpoint-tablet
    &-content
      margin-bottom: 40px

div
  box-sizing: border-box

body
  height: 100%
  margin: 0

html
  min-height: 100%