// - - - - Footer Styling - - - - //

// Placeholders
%footer-title
  font-family: 'decima_monoregular'
  color: $black-400
  font-size: 12px
  letter-spacing: 1.29px
  line-height: 14px
  text-transform: uppercase

%footer-body
  font-family: 'decima_monoregular'
  color: $black-200
  font-size: 12px
  line-height: 16px
  text-decoration: none


// Styling

.footer
  padding: 0 20px
  background-color: $black-500
  &__contact
    &-title
      @extend %footer-title
    &-address
      @extend %footer-body
      a
        @extend %footer-body
        text-decoration: underline
        &:hover
          color: white
    &-copyright
      color: $black-400
      font-size: 10px
      letter-spacing: 1.29px
      line-height: 14px
      text-transform: uppercase

  &__sub
    &-title
      @extend %footer-title
    &-list
      @extend %footer-body
      list-style: none
      padding: 0
    &-link
      @extend %footer-body
      &:hover
        text-decoration: underline
        color: white
        cursor: pointer

  &__social
    &-title
      @extend %footer-title
    &-list
      @extend %footer-body
      list-style: none
      padding: 0
    &-link
      &--github
        &:before
          +materialicon($github)
          padding-right: 10px
      &--dribbble
        &:before
          +materialicon($dribbble)
          padding-right: 10px
      &--linkedin
        &:before
          +materialicon($linkedin)
          padding-right: 10px
      &--codepen
        &:before
          +materialicon($codepen)
          padding-right: 10px

  &__top-link
    @extend %footer-title
    text-align: right
    color: white
    cursor: pointer
    &:after
      +materialicon($long-arrow-up)
      padding-left: 5px
