// - - - - Activity Feed Layout - - - - //

.activity-feed
  display: none
  &__timeline
    position: absolute
    top: 0px
    bottom: 0px
    right: 219px
    width: 1px
    min-height: 100%
    background-color: $black-200
    opacity: 0.3
  &__arrow
    position: absolute
    right: 213px
    font-size: 24px
    color: white
    opacity: 0.6
    transition: opacity .2s ease
    &:hover
      opacity: 1
    &-up
      @extend .activity-feed__arrow
      top: 70px
      &:before
        +materialicon($chevron-up)
    &-down
      @extend .activity-feed__arrow
      bottom: 70px
      &:before
        +materialicon($chevron-down)

  &__bubble
    position: relative
    //top: 45%
    //right: 200px
    float: right
    width: 40px
    height: 40px
    border-radius: 80px
    z-index: 5000
    &--hipchat
    &--trello
      @extend .activity-feed__bubble
      background-color: #3579F0
      -webkit-box-shadow: 0px 0px 0px 5px rgba(53,121,240,0.25)
      -moz-box-shadow: 0px 0px 0px 5px rgba(53,121,240,0.25)
      box-shadow: 0px 0px 0px 5px rgba(53,121,240,0.25)
    &--github

  &__info
    position: absolute
    top: 190px
    right: 120px
    &-application
      color: white
      font-size: 12px
      letter-spacing: 1.3
    &-time
      margin-top: 2px
      color: white
      opacity: 0.8
      font-size: 10px
      font-style: italic
      letter-spacing: 1


