// - - - - Blog Styling - - - -//

.blog
  padding: 40px 0
  &__header
    padding: 0 20px
  &__header-title
    @extend %section-title
  &__header-text-button
    @extend %text-button
  &__date
    @extend %date
    @media (min-width: $breakpoint-tablet)
      padding 0 20px 0 0
    @media (min-width: $breakpoint-tablet)
      padding 0 20px 0 0
  &__title
    @extend %title-link
  &__author
  &__author-image
    border-radius: 50px
    &--alex
      @extend %alex, .blog__author-image
    &--andrew
      @extend %andrew, .blog__author-image
    &--brian
      @extend %brian, .blog__author-image
    &--cam
      @extend %cam, .blog__author-image
    &--dave
      @extend %dave, .blog__author-image
    &--dayton
      @extend %dayton, .blog__author-image
    &--forrest
      @extend %forrest, .blog__author-image
    &--josh
      @extend %josh, .blog__author-image
    &--justin
      @extend %justin, .blog__author-image
  &__author-name
    @extend %author-name
  &__break-line
    background-color: $black-200
.blog-list-item
  padding: 0 20px

// Blog Post
.blog
  &-related
    padding: 40px 0 0 0
  &-header
    border-bottom: 1px solid $black-200
    text-transform: uppercase
  &-meta
    @extend %date
  &-content
    padding: 0 20px