.hero
  position: relative
  height: 400px
  width: 100%
  &__pitch
    +clearfix
    +span(10 of 12)
    position: absolute
    bottom: 20px
    @media (min-width: $breakpoint-tablet)
      +span(6 of 12)
    @media (min-width: $breakpoint-desktop)
      +span(6 of 12)
  &__scroll-diamond
    position: relative
    float: right
    width: 0
    height: 0
    margin-right: 20px
    margin-top: 355px
    &:after 
      position: absolute
      left: -35px
      top: 35px
      width: 0
      height: 0
    &:before
      position: absolute
      z-index: 100
      top: 26px
      left: -4px

.break-line
  height: 1px
  width: 100%

.intro
  clear: both
  &__text-button
    margin-top: 20px

.projects
  &__header
    +clearfix
  &__header-title
    float: left
  &__header-text-button
    float: right
  &__image
    overflow: hidden
  &__img
    max-width: 100%
  &__type
    margin-top: 2px
.projects-list
  display: flex
  flex-wrap: wrap
  @media (min-width: $breakpoint-tablet)
    margin-left: -20px
    margin-top: -20px
.projects-list-item
  margin-bottom: 20px
  max-width: 100%
  &:last-child
    margin-bottom: 40px
  @media (min-width: $breakpoint-tablet)
    @extend .projects-list-item
    flex: 1 0 calc(50% - 20px)
    box-sizing: outline-box
    margin-left: 20px
  @media (min-width: $breakpoint-desktop)
    @extend .projects-list-item
    flex: 1 0 calc(25% - 20px)
    box-sizing: outline-box
    margin-left: 20px
    
