// - - - - Contact Styling - - - - //

.contact
  &__second-column
    padding: 0
    
  &__info
    color: $black-500
    font-size: 20px
    line-height: 30px
    @media (min-width: $breakpoint-tablet)
      color: $black-500
      font-size: 30px
      line-height: 35px
    &-link
      transition: color .3s ease
      border-bottom: 2px solid $black-500
      @media (min-width: $breakpoint-tablet)
        border-bottom: none
      &:hover
        color: $brand-primary-500
        border-bottom: 2px solid $brand-primary-500
        @media (min-width: $breakpoint-tablet)
          border-bottom: 3px solid $brand-primary-500

  &__social
    &-icon
      &--github
        transition: color .3s ease
        &:hover
          color: $brand-primary-500
        &:before
          +materialicon($github)
          padding-right: 10px
      &--dribbble
        transition: color .3s ease
        &:hover
          color: $brand-primary-500
        &:before
          +materialicon($dribbble)
          padding-right: 10px
      &--linkedin
        transition: color .3s ease
        &:hover
          color: $brand-primary-500
        &:before
          +materialicon($linkedin)
          padding-right: 10px
      &--codepen
        transition: color .3s ease
        &:hover
          color: $brand-primary-500
        &:before
          +materialicon($codepen)
          padding-right: 10px