// - - - - Blog Layouts - - - // 

// Blog Homepage

.blog
  &__header
    +clearfix
    margin-bottom: 20px
  &__header-title
    float: left
  &__header-text-button
    float: right
  &__date
    clear: both
    margin-bottom: 10px
    @media (min-width: $breakpoint-tablet)
      +span (3 of 12)
    @media (min-width: $breakpoint-desktop)
      +span (2 of 12)
  &__title
    margin-bottom: 10px
    @media (min-width: $breakpoint-tablet)
      +span (9 of 12 no-gutter)
    @media (min-width: $breakpoint-desktop)
      +span (7 of 12)
      margin-bottom: 0
  &__author
    +clearfix
    @media (min-width: $breakpoint-tablet)
      +span (9 of 12 no-gutter)
      +push (3 of 12)
    @media (min-width: $breakpoint-desktop)
      +span (3 of 12 no-gutter)
      +push (0 of 12)
  &__break-line
    +span(12 of 12 no-gutter)
    +clearfix
    height: 1px
    margin-bottom: 20px
  &__author-image
    float: left
    height: 30px
    width: 30px
    @media (min-width: $breakpoint-desktop)
      float: right
  &__author-name
    float: left
    margin-left: 10px
    margin-top: 7px
    @media (min-width: $breakpoint-desktop)
      float: right
      margin-right: 10px
.blog-list-item
  +clearfix
  margin-bottom: 20px

// Blog Index
.blog-index
  &__teaser
    @extend %body-text
    margin-top: 10px
    @media (min-width: $breakpoint-tablet)
      +span (9 of 12 no-gutter)
      +push(3 of 12)
    @media (min-width: $breakpoint-desktop)
      +span (7 of 12)
      +push(2 of 12)
  &__read-more
    @extend %text-button
    display: block
    margin-top: 20px
    @media (min-width: $breakpoint-tablet)
      +span (9 of 12 no-gutter)
      +push(3 of 12)
    @media (min-width: $breakpoint-desktop)
      +span (7 of 12)
      +push(2 of 12)

// Blog Post
.blog
  &-header
    margin-top: 40px
    box-sizing: outline-box
  