// - - - - About Us Layout - - - - //

.about
  // Column Layouts
  &__first-column
    display: none
    @media (min-width: $breakpoint-tablet)
      +span(2 of 12 no-gutter)
      display: block
  &__second-column
    @media (min-width: $breakpoint-tablet)
      +span(10 of 12 no-gutter)

  // Side Scroll
  &__scroll
    &-item
      position: relative
      margin-bottom: 10px
      &:first-child
        margin-top: 40px
      &--active
        &:after
          position: absolute
          right: -24px
          content:"—"
          z-index: 5000

  // Sections
  &__intro, &__philosophy, &__process, &__team
    margin: 40px 0
  &__stack
    margin: 40px 0 0 0

  // Filters in Stack 
  &__title
    @extend %H2
    display: inline-block
  &__filters
    +clearfix
    display: block
    @media (min-width: $breakpoint-tablet)
      display: inline-block
      float: right
      margin: 0 0 20px 0
      text-align: top
    &-item
      display: inline-block
      margin-right: 10px
      @media (min-width: $breakpoint-tablet)
        margin: 0 0 0 10px

// Image Grid
.about-grid
  display: -webkit-flex
  display: flex
  clear: both
  width: 100%
  flex-wrap: wrap
  &__item
    display: inline-block
    width: calc(50% - 10px)
    margin: 0 10px 20px 0
    justify-content: flex-start
    box-sizing: outline-box
    @media (min-width: 560px)
      width: calc(33% - 10px)
    @media (min-width: $breakpoint-tablet)
      width: calc(25% - 10px)
  &__image
    display: flex
    height: 80px
    -webkit-align-items: center
    align-items: center
  &__img
    align-items: center
    width: auto
    max-width: 100px
    max-height: 40px
  &__team-headshot
    width: 100%
    height: auto
    max-width: 100%
  &__text
    margin-top: 5px

