// - - - Contact Page Layout - - - //

.contact
  @media (min-width: $breakpoint-tablet)
    height: 100vh

  &__first-column
    float: top
    height: 400px
    @media (min-width: $breakpoint-tablet)
      float: left
      width: 50%
      height: 100%

  &__second-column
    float: top
    margin: 0
    display: -webkit-box
    display: -moz-box
    display: -ms-flexbox
    display: -webkit-flex
    display: flex
    align-items: center
    justify-content: center
    @media (min-width: $breakpoint-tablet)
      float: right
      width: 50%
      height: 100%

  &__logo
    width: 80%
    height: auto
    margin-bottom: 20px
    @media (min-width: $breakpoint-tablet)
      margin-bottom: 40px

  &__info
    margin: 40px 

  &__social
    margin-top: 40px
    &-icon
      &--github, &--dribbble, &--linkedin, &--codepen
        display:inline-block
        