// - - - - Footer Layout - - - - //

// Placeholders

%footer-title
  margin-top: 20px

%footer-body
  margin-top: 10px

// Layout

.footer
  height: auto
  &__contact
    +span(8 of 12)
    &-title
    &-address
      @extend %footer-body
      a
        @extend %footer-body
        display: block
        margin-top: 0
    @media (min-width: 550px)
      +span(4 of 12)
    @media (min-width: $breakpoint-tablet)
      +span (3 of 12)
      display: inline
    &-copyright
      margin: 20px 0

  &__sub
    display: none
    &-title
    &-list
    &-link
      margin-top: 0
    @media (min-width: 550px)
      +span (4 of 12)
      display: inline
    @media (min-width: $breakpoint-tablet)
      +span (3 of 12)
      display: inline

  &__social
    display: none
    &-title
    &-list
    &-link
      &--github
      &--dribbble
      &--linkedin
      &--codepen
    @media (min-width: $breakpoint-tablet)
      +span (3 of 12)
      display: inline

  &__top-link
    +span(4 of 12 no-gutter)
    @media (min-width: $breakpoint-tablet)
      +span (3 of 12 no-gutter)
      display: inline