.hero
  @extend %background-fill
  background-image: url(/app/themes/blog/assets/images/hero-background-image.jpg) 
  &__pitch
    @extend %hero-text
    padding: 20px
  &__scroll-diamond
    border-top: 10px solid transparent
    border-right: 35px solid transparent
    border-bottom: 35px solid transparent
    border-left: 35px solid transparent
    transition: border-bottom-color 0.4s ease
    border-bottom-color: $brand-primary-500
    &:hover, &:active
      border-bottom-color: $brand-primary-600
      &:after
        border-top-color: $brand-primary-600
    &:after 
      content: ''
      border: 35px solid transparent
      transition: border-top-color 0.4s ease
      border-top-color: $brand-primary-500
    &:before
      +materialicon($long-arrow-down)
      font-size: 18px
      color: white

.break-line
  clear: both
  background-color: $black-200

.intro
  padding: 50px 20px 40px 20px
  &__title
    @extend %intro-text
  &__text-button
    @extend %text-button

.projects
  padding: 40px 0 0 0
  &__header
    padding: 0 20px
  &__header-title
    @extend %section-title
  &__header-text-button
    @extend %text-button
  &__image
  &__title
    @extend %title-link
    padding-top: 5px
  &__type
    color: $black-500
    font-family: decima_monoregular
    font-size: 12px
    line-height: 14px
    text-transform: uppercase
.projects-list
  padding: 20px 20px 0 20px
.projects-list-item
